.Profile-card {
    border-radius: 1.5rem;
    display: flex;
    flex-direction: column;
    position: relative;
    gap: 1rem;
    overflow-x: clip;
    background: var(--cardColor);
}

.Profile-images {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.Profile-images > img:nth-of-type(1) {
    width: 100%;
}

.Profile-images > img:nth-of-type(2) {
    width: 6rem;
    border-radius: 50%;
    position: absolute;
    bottom: -3rem;
    box-shadow: var(--profileShadow);
}

.Profile-name {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 3rem;
    gap: 10px;
}

.Profile-name > span:nth-child(1) {
    font-weight: bold;
}

.follow-status {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 0.75rem;
}

.follow-status > hr {
    width: 85%;
    border: 1px solid var(--hrColor);
}

.follow-status > div {
    display: flex;
    gap: 1rem;
    flex-direction: row;
    width: 80%;
    justify-content: space-around;
    align-items: center;
}

.follow {
    display: flex;
    flex-direction: column;
    gap: 0.4rem;
    align-items: center;
    justify-content: center;
}

.follow > span:nth-of-type(1) {
    font-weight: bold;
}

.follow > span:nth-of-type(2) {
    color: var(--gray);
    font-size: 13px;
}

.vl {
    height: 150%;
    border-left: 2px solid var(--hrColor);
}

.Profile-card > span {
    font-weight: bold;
    color: orange;
    align-self: center;
    margin-bottom: 1rem;
    cursor: pointer;
}