.PostShare {
    display: flex;
    gap: 1rem;
    background-color: var(--cardColor);
    padding: 1rem;
    border-radius: 1rem;
}

.PostShare > img {
    border-radius: 50%;
    width: 3rem;
    height: 3rem;
}

.PostShare > div {
    display: flex;
    flex-direction: column;
    width: 90%;
    gap: 1rem;
}

.PostShare > div > input {
    background-color: var(--inputColor);
    border-radius: 10px;
    font-size: 17px;
    border: none;
    padding: 10px;
    outline: none;
}

.post-options {
    display: flex;
    justify-content: space-between;
}

.option {
    padding: 5px;
    padding-left: 10px;
    padding-right: 10px;
    border-radius: 10px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.option:hover {
    cursor: pointer;
}

.ps-btn {
    padding: 5px;
    padding-left: 20px;
    padding-right: 20px;
    font-size: 12px;
}
